@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@300&family=Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@300&family=Satisfy&display=swap);
* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}
body {
	font-size: 65.5%;
}

:root {
	--menuColor: #4e4e4e;
	--primaryColor: #fd7b72;
	--btnBeforeBg : #fff;
	--menuHoverColor: rgb(252, 52, 52);
}
nav {
	width: 100%;
	height: 12vh;
	display: flex;
	justify-content: center;
	align-items: center;
	/* background-color: red; */
	position: relative;
}

nav ul {
	list-style-type: none;
	display: flex;
	justify-content: center;
	align-items: center;
	/* background-color: blue; */
	/* padding: 30px; */
}

nav ul li {
	color: #4e4e4e;
	color: var(--menuColor);
	padding: 20px 20px;
	font-size: 1.2rem;
	font-weight: bold;
	font-family: sans-serif;
}
li.admiBtn{
	line-height: 12vh;
}
li a.adminBtn_link{
	position: absolute;
	right: 5%;
	top:50%;
	transform: translateY(-50%);
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 1rem;
	border-radius: 0.7rem;
	background-color: #fd7b72;
	background-color: var(--primaryColor);
	color: #ffff;
	overflow: hidden;
	
	

}
li a.adminBtn_link:hover{
	color: #fff !important;
	opacity: 0.7;
}

li a.adminBtn_link::before{
	content: "";
	position: absolute;
	width: 100%;
	height: 2rem;
	background-color:#fff;
	background-color:var(--btnBeforeBg);
	bottom: 0;
	left: -100%;
	transition: all 0.5s;
	transform: rotate(45deg);
}

li a.adminBtn_link:hover::before{
	left: 100%;
}



nav ul li a {
	color: #4e4e4e;
	color: var(--menuColor);
	text-decoration: none;
	font-weight: 500;
}

nav ul li a:hover {
	color: #fd7b72;
	color: var(--primaryColor);
	transition: 0.1s;
}

.menuToggle{
	width: 6vw;
	height: 6vh;
	display: flex;
	justify-content: start;
	align-items: center;
	font-size: 1.8rem;
	color: #fd7b72;
	color: var(--primaryColor);
	display: none;
}
.menubar{
	width: 100%;
	height: 30vh;
	display: flex;
	justify-content: center;
	align-items: center;
	display: none;
	transition: all 0.5s;

}

.menubar ul{
	list-style: none;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.menubar ul li{
	padding: 1rem;
	font-size: 1.2rem;
}

.menubar ul li a{
	text-decoration: none;
	color: #4e4e4e;
	color: var(--menuColor);
}
.menubar ul li a:hover{
	text-decoration: none;
	color: #fd7b72;
	color: var(--primaryColor);
}



@media screen and (max-width: 700px){
	nav ul{
		display: none;
	}
	.menuToggle{
		display: block;
		margin-top: 2rem;
		
	}

	.menuToggle:hover{
		color: rgb(252, 52, 52);
		color: var(--menuHoverColor);
		cursor: pointer;
	}
	.menubar{
		display: none;
		transition: all 0.5s;
		
	}

	.act_toggle{
		display: block;
		justify-content: center;
		transition: all .5s linear;
	}
	.adminBtn_link{
		font-size: 1.2rem;
		text-decoration: none;
		border: none;
		outline: none;
		padding: .6rem;
		border-radius: .7rem;
		background-color: #fd7b72;
		background-color: var(--primaryColor);
		color: #fff;
		position: relative;
		overflow: hidden;

	}
	.adminBtn_link::before{
		content: "";
		width: 100%;
		height: 2rem;
		background-color: #fff;
		position: absolute;
		left: -100%;
		transform: rotate(45deg);
		transition: all 0.5s;
	}

	.adminBtn_link:hover::before{
		left: 100%;
	}
}


:root {
	--primary: #fd7b72;
	--secondary: #4e4e4e;
	--error: #ff4a4a;
	--bgColor: #f7f1e3;
}

/* base styles & title */
body {
	font-family: "Noto Serif";
	color: #4e4e4e;
	color: var(--secondary);
	text-decoration: none;
	background-color: #f7f1e3;
	background-color: var(--bgColor);
}
.App {
	max-width: 90%;
	margin: 0 auto;
}
.title h1 {
	color: #fd7b72;
	color: var(--primary);
	font-size: 1.5rem;
	letter-spacing: 2px;
	font-weight: bold;
}
.title h2,
.title p {
	text-align: center;
}
.title h2 {
	margin-top: 60px;
	font-size: 3rem;
	margin-bottom: 50px;
}

/* upload form styles */
form {
	margin: 30px auto 10px;
	text-align: center;
}
label input {
	height: 0;
	width: 0;
	opacity: 0;
}
label {
	display: block;
	width: 40px;
	height: 40px;
	border: 1px solid #fd7b72;
	border: 1px solid var(--primary);
	border-radius: 50%;
	margin: 10px auto;
	line-height: 30px;
	color: #fd7b72;
	color: var(--primary);
	font-weight: bold;
	font-size: 24px;
	display: flex;
	justify-content: center;
	align-items: center;
}
label:hover {
	background: #fd7b72;
	background: var(--primary);
	color: white;
	cursor: pointer;
}
.output {
	height: 60px;
	font-size: 0.8rem;
}
.error {
	color: #ff4a4a;
	color: var(--error);
}

/* progress bar styles */
.progress-bar {
	height: 5px;
	background: #fd7b72;
	background: var(--primary);
	margin-top: 20px;
}

/* image grid styles */
.img-grid {
	margin: 2rem auto;
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-gap: 40px;
}
.img-wrap {
	overflow: hidden;
	height: 0;
	padding: 40% 0;
	/* padding controls height, will always be perfectly square regardless of width */
	position: relative;
	opacity: 0.8;
	border-radius: 2rem;
}
.img-wrap img {
	min-width: 100%;
	min-height: 100%;
	max-width: 150%;
	position: absolute;
	top: 0;
	left: 0;
}

/* modal styles */
.backdrop {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.5);
}
.backdrop img {
	display: block;
	max-width: 60%;
	max-height: 80%;
	margin: 60px auto;
	box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
	border: 3px solid white;
}

/* back to top button */


#scroll_top a{
	position: fixed;
	bottom: 2rem;
	right: 1rem;
	z-index: 1;
	background-color: #fd7b72;
	background-color: var(--primary);
	padding: 0.7rem 0.7rem;
	border-radius: 2rem;
	color: #fff;
	font-size: 3rem;
}

#scroll_top a.isActive{
	background-color: transparent;
	color:#fd7b72;
	color:var(--primary);
	border: .2rem solid #fd7b72;
	border: .2rem solid var(--primary);
}

@media (max-width: 700px){
	.backdrop img {
	display: block;
	max-width: 90%;
	max-height: 80%;
	margin: 10rem auto;
	box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
	border: 3px solid white;
	}

	.img-grid {
	margin: 2rem auto;
	display: grid;
	grid-template-columns: 1fr;
	grid-gap: 40px;
	border-radius: 2rem;
	}
}
.card_container {
	max-width: 20rem;
	width: 20rem;
	max-height: 20rem;
	height: 20rem;
	margin: 2rem;
	position: relative;
	overflow: hidden;
	border: .1rem solid rgb(201, 197, 197);
	transition: transform .5s ease;
	border-radius: 2rem;
	overflow: hidden;
}

.card_container:hover {
	transform: scale(1.02);
	cursor: pointer;
	box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
}

.card_container .img_container {
	min-width: 30rem;
	max-height: 50vh;
	overflow: hidden;
	top: 0;
	left: 0;
	right: 0;
	margin-bottom: 2rem;
	object-fit: contain;
}

.card_container .img_container img {
	width: 100%;
	object-fit: contain;
}

.card_container .img_container img:hover {
	opacity: 0.9;
}

.card_container .title {
	width: 100%;
	padding: 1rem;
	position: absolute;
	bottom: 0;
	left: 0;
	display: flex;
	justify-content: center;
	background-color: rgb(255, 255, 255);
	align-items: center;
}

.title h3 {
	font-size: 1.5rem;
	font-family: "Noto Serif";
}

.mainContainer {
	width: 100%;
	height: auto;
	margin-bottom: 5rem;
}

.mainContainer .gridContainer {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}

:root {
	--menuColor: #000000;
	--primaryColor: #fd7b72;
	--headingFontSize: 2rem;
}

html {
	scroll-behavior: smooth;
}
* a {
	color: #fd7b72;
	color: var(--primaryColor);
}

.footerContainer {
	width: 100%;
	height: 50%;
}

footer {
	display: flex;
	flex-wrap: wrap;
	height: 40%;
	justify-content: space-around;
	align-items: flex-start;
}
footer .leftcontent,
footer .centercontent,
footer .rightcontent {
	width: 30.222%;
	height: 40vh;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

footer .leftcontent h3 {
	flex: 1 1;
	font-size: 2rem;
	font-size: var(--headingFontSize);
	font-weight: bold;
	line-height: 1.5;
	letter-spacing: 0.07rem;
	margin-bottom: 1.5rem;
}

footer .leftcontent p {
	flex: 1 1;
	font-size: 1rem;
	font-weight: 500;
	letter-spacing: .01rem;
	line-height: 1.5;
	margin-top: 2rem;
}

.centercontent .usefulLinks h3 {
	font-size: 2rem;
	font-size: var(--headingFontSize);
	font-weight: bold;
	padding-bottom: 2rem;
	letter-spacing: 0.07rem;
}

.centercontent .Links_ {
	position: relative;
}
.centercontent .Links_::before {
	content: "";
	width: 70%;
	border-bottom: .2rem solid #fd7b72;
	border-bottom: .2rem solid var(--primaryColor);
	position: absolute;
	top: 0;
	margin-top: -1.5rem;
}
.centercontent .Links_ ul {
	list-style: none;
}

.centercontent .Links_ ul li {
	font-size: 1rem;
	font-weight: bold;
	padding: .5rem;
}

.centercontent .Links_ ul li a {
	text-decoration: none;
	color: #4e4e4e;
	margin-bottom: 2rem;
}

.centercontent .Links_ ul li a:hover {
	border-left: 0.3rem solid #fd7b72;
	border-left: 0.3rem solid var(--primaryColor);
	padding-left: .5rem;
	transition: all 0.3s;
}

.rightcontent h3 {
	font-size: 2rem;
	font-size: var(--headingFontSize);
	font-weight: bold;
	padding-bottom: 0.5rem;
	letter-spacing: 0.07rem;
}

.rightcontent p {
	flex: 1 1;
	font-size: 1rem;
	font-weight: 500;
	letter-spacing: .01rem;
	line-height: 1.5;
	margin-top: 2rem;
}

.rightcontent .contact {
	position: relative;
}
.rightcontent .contact::after {
	content: "";
	width: 70%;
	border-bottom: .2rem solid #fd7b72;
	border-bottom: .2rem solid var(--primaryColor);
	position: absolute;
}

.footer_cp {
	height: 6vh;
	font-size: 1rem;
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
	text-align: center;
	padding: 2rem 0rem;
}

.footer_cp::before {
	content: "";
	position: absolute;
	top: 0;
	border-top: .2rem solid #fd7b72;
	border-top: .2rem solid var(--primaryColor);
	width: 100%;
}

.developerName {
	color: #fd7b72;
	color: var(--primaryColor);
	font-weight: bold;
}
@media (max-width: 976px) {
	footer .leftcontent {
		width: 100%;
		height: auto;
		margin-bottom: 2rem;
		margin-top: 2rem;
	}

	footer .centercontent {
		width: 100%;
		height: auto;
		margin-bottom: 2rem;
	}
	footer .rightcontent {
		width: 100%;
		height: auto;
		margin-bottom: 2rem;
	}
	.centercontent .Links_::before {
		width: 100%;
		border-width: .15rem;
		left: 0;
	}
	.rightcontent .contact::after {
		width: 100%;
		border-width: .15rem;
	}
	.leftcontent,
	.centercontent,
	.rightcontent h3 {
		text-align: center;
	}
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

:root {
    --bgColor: #f7f1e3;
    --primary: #fd7b72;
    --secondary: #4e4e4e;
    --fontSize: 1.5rem;
    --beforeBtnColor: #fff;


}

body {
    font-family: "Noto Serif";
    background-color: #f7f1e3;
    background-color: var(--bgColor);
}

.modal__ {
    width: 60%;
    height: 60vh;
    margin: 5rem auto;
    position: relative;
}

.modal__ h1 {
    font-size: 2rem;
    display: flex;
    justify-content: center;
    padding-bottom: 1rem;
    position: relative;
}

.borderanimation {
    width: 30%;
    height: .3rem;
    background-color: #fd7b72;
    background-color: var(--primary);
    margin: 0 auto;
    border-radius: 50%;
    position: relative;
    overflow: hidden;
}

.borderanimation::after {
    content: "";
    width: 1rem;
    border-bottom: .5rem solid #fff;
    position: absolute;
    bottom: 0;
    left: 40%;
    animation: moveright 0.7s linear infinite;

}

@keyframes moveright {
    from {
        left: 0%;
    }

    to {
        left: 100%;
    }
}

label.__formLabel {
    font-size: 1.2rem;
    display: block;
    width: 100%;
    height: 2rem;
    border: none;
    border-radius: none;
    margin: 0 0;
    line-height: 30px;
    color: #4e4e4e !important;
    color: var(--secondary) !important;
    display: flex;
    justify-content: flex-start !important;
}

label.__formLabel {
    background-color: transparent;
    cursor: default;
}

input[type=email],
input[type=password] {
    width: 100%;
    padding: .7rem .7rem;
    font-size: 1.2rem;
    border: .1rem solid #ccc;
    outline: none;
    margin: 1rem 0 1rem 0;
    border-radius: 1rem;
}

.modal__ .model_content {
    margin-top: 5rem;
    min-width: 30rem;
    width: 20rem;
    height: auto;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

button.loginBtn {
    width: 100%;
    padding: 0.7rem 0rem;
    margin: 3rem 0rem;
    color: #fff;
    background-color: #fd7b72;
    background-color: var(--primary);
    border: none;
    font-size: 1.5rem;
    font-size: var(--fontSize);
    position: relative;
    overflow: hidden;
    border-radius: 1rem;
    outline: none;

}

button.loginBtn::before {
    content: "";
    width: 100%;
    height: 3rem;
    background-color: #fff;
    background-color: var(--beforeBtnColor);
    position: absolute;
    left: -100%;
    transform: rotate(45deg);
    transition: all .5s linear;

}

button.loginBtn:hover::before {
    left: 100%;
}

button.loginBtn:hover {
    cursor: pointer;
    opacity: 0.7;
}

.animateModal {
    animation: animateModal 0.5s;
}

@keyframes animateModal {
    from {
        transform: scale(0);
    }

    to {
        transform: scale(1);
    }
}


@media screen and (max-width: 532px) {
    .modal__ {
        width: 100%;
        margin-top: 4rem;
    }

    .modal__ .model_content {
        margin-top: 3rem;
        min-width: 45%;
    }
}

